import React, { createContext, useContext, useState, useEffect } from 'react';
import { requestApi } from '../../shared/utils/requestApi';
import { BASE_API_URL } from '../../shared/utils/url';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
    const [username, setUsername] = useState(localStorage.getItem('username'));
    const [accessToken, setAccessToken] = useState(localStorage.getItem('accessToken'));
    const [refreshToken, setRefreshToken] = useState(localStorage.getItem('refreshToken'));
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [isLoading, setIsLoading] = useState(true); // Tracks initial auth check

    // Checks if a JWT token is expired
    const isTokenExpired = (token) => {
        if (!token) return true;
        try {
            const payloadBase64 = token.split('.')[1];
            const payloadJson = atob(payloadBase64);
            const payload = JSON.parse(payloadJson);
            const now = Math.floor(Date.now() / 1000);
            return payload.exp < now;
        } catch (error) {
            console.error("Error decoding token:", error);
            return true; // Malformed or unreadable token is considered expired
        }
    };

    // Attempts to refresh the access token
    const refreshAuthToken = async () => {
        if (!refreshToken || isTokenExpired(refreshToken)) {
            console.log("No refresh token or refresh token expired. Logging out.");
            logout(); // Force logout if refresh token is gone or expired
            return false;
        }

        const { isSuccess, data, message } = await requestApi(BASE_API_URL + '?path=token/refresh', { refreshToken });
        if (!isSuccess) {
            console.error('Failed to refresh token:', message);
            logout(); // Logout on refresh failure
            return false;
        }

        setAccessToken(data.accessToken);
        localStorage.setItem('accessToken', data.accessToken);
        setIsAuthenticated(true);
        return true;
    };

    // Handles user login
    const login = async (username, password) => {
        const { data, isSuccess, message } = await requestApi(BASE_API_URL + "?path=login", { username, password });
        if (isSuccess) {
            setAccessToken(data.accessToken);
            setRefreshToken(data.refreshToken);
            setUsername(username);
            localStorage.setItem('accessToken', data.accessToken);
            localStorage.setItem('refreshToken', data.refreshToken);
            localStorage.setItem('username', username);
            setIsAuthenticated(true);
            return true;
        }

        logout();
        return false;
    };

    // Handles user logout
    const logout = () => {
        setAccessToken(null);
        setRefreshToken(null);
        setUsername(null);
        setIsAuthenticated(false);
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        localStorage.removeItem('username');
    };

    // Initial check when the AuthProvider mounts
    useEffect(() => {
        const checkAuth = async () => {
            if (accessToken && !isTokenExpired(accessToken)) {
                setIsAuthenticated(true);
            } else if (refreshToken && !isTokenExpired(refreshToken)) {
                // If access token is expired but refresh token is valid, try to refresh
                await refreshAuthToken();
            } else {
                // No valid tokens found, user is not authenticated
                setIsAuthenticated(false);
                logout(); // Ensure tokens are cleared
            }
            setIsLoading(false); // Auth check is complete
        };
        checkAuth();
    }, []); // Run only once on mount

    const value = {
        username,
        accessToken,
        refreshToken,
        isAuthenticated,
        isLoading,
        login,
        logout,
        refreshAuthToken,
        isTokenExpired, // Expose for components to check before API calls        
    };

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    return useContext(AuthContext);
};