import { useCallback } from 'react';
import { useAuth } from '../../assistant/Contexts/AuthContext';
import { requestApi } from '../utils/requestApi';
import { BASE_API_URL } from '../utils/url';

export function useApiClient() {
    const { accessToken, refreshToken, refreshAuthToken, isTokenExpired, logout } = useAuth();

    // This is the core function that makes a request and handles token refresh/retry
    const makeRequestToProtectedEndpoint = useCallback(async (url, jsonReqBodyWithoutAuth, retryCount = 0) => {
        const MAX_RETRIES = 1; // Allow one retry after a 401

        let currentAccessToken = accessToken; // Get the current token from context closure

        // --- Pre-flight token check (if token is already known to be expired before even trying) ---
        if (currentAccessToken && isTokenExpired(currentAccessToken) && retryCount === 0) {
            console.log("Pre-flight: Access token expired. Attempting refresh...");
            const refreshSuccess = await refreshAuthToken();
            if (!refreshSuccess) {
                // If refresh failed, AuthContext should have already logged out the user.
                throw new Error("Session expired. Please log in again.");
            }
            // After successful refresh, retrieve the updated token.
            // It might not be updated in the component's state synchronously,
            // so often getting from localStorage is safer immediately after refresh.
            currentAccessToken = localStorage.getItem('accessToken');
            if (!currentAccessToken) {
                throw new Error("Failed to obtain new access token after refresh.");
            }
        }

        const jsonWithAuth = { ...jsonReqBodyWithoutAuth, authorizationHeader: `Bearer ${currentAccessToken}` };
        const response = await requestApi(url, jsonWithAuth);

        // --- Handle 401 Unauthorized ---
        if (!response.isSuccess && response.statusCode === 401) {
            if (retryCount < MAX_RETRIES) {
                console.warn("API returned 401. Attempting token refresh and retry...");
                const refreshSuccess = await refreshAuthToken(); // This updates AuthContext state
                if (refreshSuccess) {
                    // After successful refresh, immediately retry the request
                    // Pass 'true' for isRetry or increment retryCount to avoid infinite loops
                    return await makeRequestToProtectedEndpoint(url, jsonReqBodyWithoutAuth, retryCount + 1);
                } else {
                    // Refresh token failed, probably also expired or invalid
                    console.error("Token refresh failed during 401 retry. Logging out.");
                    logout(); // Ensure full logout
                    throw new Error("Session expired. Please log in again.");
                }
            } else {
                // Max retries reached, still getting 401
                console.error("Authentication failed even after retry. Logging out.");
                logout();
                throw new Error("Authentication failed. Please log in again.");
            }
        }

        return response;
    }, [accessToken, refreshToken, refreshAuthToken, isTokenExpired, logout]);

    // Specific API call wrapper for Hugging Face
    const postHuggingFace = useCallback(async (prompt) => {
        return makeRequestToProtectedEndpoint(BASE_API_URL + "?path=huggingface", {
            model: 'deepseek/deepseek-prover-v2-671b',
            prompt
        });
    }, [makeRequestToProtectedEndpoint]);

    return {
        postHuggingFace
    };
}