import React from "react";

export default function Card({ children }) {
    return (
        <div style={{
            backgroundColor: '#fff',
            borderRadius: '8px',
            boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)', // Soft shadow for depth
            padding: '40px', // Internal padding
            maxWidth: '400px', // Maximum width for the card on larger screens
            width: '100%', // Full width on smaller screens
            textAlign: 'center', // Center align text within the card
            position: 'relative', // Needed for z-index to work correctly against absolute children
            zIndex: 1 // Ensure the card is on top of the circles
        }}>{children}</div>
    );
}