/**
 * Call to appscript api and return response.
 * @param {string} url The appscript url to send the request to (.../exec?path=login, .../exec?path=token/refresh, or .../exec?path=huggingface)
 * @param {json} jsonReqBody The json object to send to 
 * @returns Return api response in json format (data,message,statusCode)
 */
export async function requestApi(url, jsonReqBody) {
  try {
    const response = await fetch(url, {
      method: 'POST',
      /* This will not work because it will cause preflight request (CORS), simply use application/x-www-form-urlencoded instead of content-type: application/json (to avoid CORS)
      headers: {
          'Content-Type': 'application/json',
      },
      body: JSON.stringify(jsonReqBody),*/
      body: new URLSearchParams(jsonReqBody)
    });
    const responseData = await response.json();

    if (responseData.error) {
      return { data: null, isSuccess: false, message: responseData.error, statusCode: responseData.status };
    } else {
      return { data: responseData, isSuccess: true, message: 'success', statusCode: 200 };
    }
  } catch (error) {
    return { data: null, isSuccess: false, message: error, statusCode: 500 };
  }
}